#star {
  width: 1px;
  height: 1px;
  background: transparent;
  box-shadow: 0px 0px #FFF;
  -webkit-animation: starfield 10s linear infinite;
          animation: starfield 10s linear infinite; }
  #star:after {
    content: " ";
    position: absolute;
    -webkit-transform: translate(50vw, 50vh);
            transform: translate(50vw, 50vh);
    width: 1px;
    height: 1px;
    background: transparent;
    box-shadow: 50vw 50vh #FFF; }

@-webkit-keyframes starfield {
  from {
    -webkit-transform: perspective();
            transform: perspective();
    opacity: 1; }
  to {
    -webkit-transform: translate(58, 290);
            transform: translate(58, 290);
    opacity: 1; } }

@keyframes starfield {
  from {
    -webkit-transform: perspective();
            transform: perspective();
    opacity: 1; }
  to {
    -webkit-transform: translate(58, 290);
            transform: translate(58, 290);
    opacity: 1; } }

